<template>
  <div>
    <template v-if="showErrors()">
      <div v-for="(error, index) in listOfErrors()" :key="index" class="error">
        {{ error }}
      </div>
    </template>
  </div>
</template>

<script>
/**
 * @description component to show errors
 * @name "ShowErrors"
 */
export default {
  name: "ShowErrors",

  /**
   * @vue-prop {Array or Object} control
   * @vue-prop {String} field
   */
  props: {
    control: {
      type: [Array, Object],
      default: () => {}
    },
    field: {
      type: String,
      default: ""
    }
  },
  /**
   * @description data variables used by component
   * @returns {variables}
   */
  data() {
    return {
      errorMessages: {
        required: () => this.fieldName(),
        decimal: () => this.decimal,
        numeric: () => this.numeric,
        email: () => this.email,
        float: () => this.numeric,
        minLength: () => this.validPassword,
        maxLength: () => this.validPassword,
        sameAsPassword: () => this.matchPassword,
        hour_greater_res: () => this.hourGreater,
        hour_greater_free: () => this.hourGreater,
        hour_less_or_greater_res: () => this.hourGreaterOrLess,
        res_end_cancel_lessthan: () => this.resEndCancelLessThan,
        max_people_greaterthan: () => this.maxPeopleGreaterThan,
        sameAsEmail: () => this.email,
        date_greater: () => this.dateGreaterThan,
        minValue: () => this.minValue,
        phoneMaxLenth: () => this.phoneMaxLenth,
        repeatDateBetween: () => this.repeatDateLimit,
        res_start_lessthan30: () => this.resStartLessThan30,
        phoneNumber: () => this.phoneNumber,
        phoneMinLength: () => this.phoneMinLength,
        free_time_end_less_or_greater: () => this.freeTimeEnd,
        free_time_start_required: () => this.freeTimeStartRequired,
        anotherOrganizationNameLength: () => this.anotherOrganizationNameLength,
        groupNameMaxLength: () => this.groupNameMaxLength,
        mustBeGreaterThanZero : () => this.mustBeGreaterThanZero,
        characterMinLength: () => this.characterMinLength
      },
      canShowError: false
    };
  },
  /**
   * @description reactive computed Values
   * @vue-computed
   */
  computed: {
    /**
     * @description message object
     * @returns {String}
     */
    repeatDateLimit() {
      return this.$t("REPEATDATELIMIT");
    },
    /**
     * @description message object
     * @returns {String}
     */
    pleaseProvide() {
      return this.$t("PLEASEPROVIDE");
    },
    /**
     * @description message object
     * @returns {String}
     */
    requireField() {
      return this.$t("REQUIREDFIELD");
    },
    /**
     * @description message object
     * @returns {String}
     */
    decimal() {
      return this.$t("DECIMAL");
    },
    /**
     * @description message object
     * @returns {String}
     */
    email() {
      return this.$t("EMAIL");
    },
    /**
     * @description message object
     * @returns {String}
     */
    numeric() {
      return this.$t("NUMERIC");
    },
    /**
     * @description message object
     * @returns {String}
     */
    validPassword() {
      return this.$t("VALIDPASSWORD");
    },
    /**
     * @description message object
     * @returns {String}
     */
    matchPassword() {
      return this.$t("MATCHPASSWORD");
    },
    /**
     * @description message object
     * @returns {String}
     */
    hourGreater() {
      return this.$t("HOURGREATER");
    },
    /**
     * @description message object
     * @returns {String}
     */
    resEndCancelLessThan() {
      return this.$t("RESERVATIONENDCANCELLESS");
    },
    /**
     * @description message object
     * @returns {String}
     */
    resStartLessThan30() {
      return this.$t("RESERVATIONSTARTDATELIMIT");
    },
    /**
     * @description message object
     * @returns {String}
     */
    maxPeopleGreaterThan() {
      return this.$t("MAXPEOPLE");
    },
    /**
     * @description message object
     * @returns {String}
     */
    mustBeGreaterThanZero() {
      return this.field + this.$t("MUSTBEGREATERTHANZERO");
    },
    /**
     * @description message object
     * @returns {String}
     */
    dateGreaterThan() {
      return this.$t("DATEGREATER");
    },
    /**
     * @description message object
     * @returns {String}
     */
    minValue() {
      return this.$t("DATEGREATER");
    },
    /**
     * @description message object
     * @returns {String}
     */
    phoneMaxLenth() {
      return this.$t("PHONEMAXLENTH");
    },
    phoneNumber() {
      return this.$t("PHONENUMBERERRMSG");
    },
    phoneMinLength() {
      return this.$t("MOBILENOLENGTHVALIDATIONERRORMESSAGE");
    },
    characterMinLength(){
      return this.$t("LOGINIDLENGTHVALIDATIONMESSAGE");
    },
    hourGreaterOrLess() {
      return this.$t("HOURGREATERORLESS");
    },
    freeTimeEnd() {
      return this.$t("FREETIMEEND");
    },
    freeTimeStartRequired() {
      return this.$t("FREETIMESTARTREQUIRED");
    },
    groupNameMaxLength() {
      return this.$t("MAXSTRINGLENGTH");
    },
    anotherOrganizationNameLength() {
      return this.$t("MAXSTRINGLENGTH");
    }
  },
  methods: {
    /**
     * @description function to fetch form field names
     * @returns {any}
     */
    fieldName() {
      if (this.field) {
       
        return this.pleaseProvide + " " + this.field;
      }
      return this.requireField;
    },

    /**
     * @description function to fetch error messages of the form
     * @param {any} type
     * @param {any} params
     * @returns {array of messages}
     */
    getMessage(type, params) {
      return this.errorMessages[type](params);
    },
    /**
     * @description function which returns whether form is valid or not
     * @returns {any}
     */
    showErrors() {
      return (
        this.control.$invalid && this.control.$anyError && this.control.$dirty
      );
    },
    /**
     * @description function fetches list of errors
     * @returns {array}
     */
    listOfErrors() {
      const params = this.control.$params;
      return Object.keys(params).map(field => {
        if (!this.control[field]) {
          return this.getMessage(field, params);
        }
      });
    }
  }
};
</script>
<style scoped lang="scss">
$error_color: #FA178C;
.error {
  font-size: 11px;
  color: $error_color;
}
</style>
